<template>
  <el-dialog
      :title="$t('user.modifwd.title')"
      :visible.sync="dialogVisible"
      :close-on-click-modal=false
      :close-on-press-escape=false
      :before-close="handleCloseDialog"
      width="580px" class="pwd-dialog">
      <div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="220px">
          <el-form-item :label="$t('user.modifwd.old') + ':'" prop="old_password">
            <el-input v-model.trim="ruleForm.old_password" type="password"></el-input>
          </el-form-item>
          <el-form-item :label="$t('user.modifwd.new') + ':'" prop="new_password">
            <el-input v-model.trim="ruleForm.new_password" type="password"></el-input>
          </el-form-item>
          <el-form-item :label="$t('user.modifwd.confirm') + ':'" prop="new_confirmpwd">
            <el-input v-model.trim="ruleForm.new_confirmpwd" type="password"></el-input>
          </el-form-item>
          <div class="btn-group appeal-btn">
            <el-button type="primary"  @click="formSubmit('ruleForm')" style="margin-left: 220px;">{{ $t('dialog.modify') }}</el-button>
          </div>
        </el-form>
      </div>
    </el-dialog>
</template>

<script>
  import { editPassword } from '@/api/user'
  import { loginPwd } from '@/utils/validate'
  import md5 from 'js-md5'
  export default {
    props: {
      dialogVisible: Boolean
    },
    data() {
      const validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.$t('errorTips.withoutPwd')))
        } else if (value.length < 6) {
          callback(new Error(this.$t('errorTips.longPwd')))
        } else if (value.length > 20) {
          callback(new Error(this.$t('errorTips.longLimitPwd')))
        } else if (!loginPwd(value)) {
          callback(new Error(this.$t('errorTips.rulePwd')))
        } else {
          callback()
        }
      }
      const validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.$t('errorTips.surePwd')))
        } else if (value !== this.ruleForm.new_password) {
          callback(new Error(this.$t('errorTips.surePwdError')))
        } else {
          callback()
        }
      }
      return {
        googleInfo: {},
        ruleForm: {
          old_password: null,
          new_password: null,
          new_confirmpwd: null
        },
  
        rules: {
          old_password: [
            { required: true, message: this.$t('user.modifwd.tips.noold'), trigger: 'blur' }
          ],
          new_password: [
            { required: true, message: this.$t('user.modifwd.tips.nonew'), trigger: 'blur' },
            { validator: validatePass, trigger: 'blur' }
          ],
          new_confirmpwd: [
            { required: true, message: this.$t('user.modifwd.tips.nocomfirm'), trigger: 'blur' },
            { validator: validatePass2, trigger: 'blur' }
          ]
        },
        validCode: true,
        validTip: this.$t('verifyCode.title'),
        questions: {}
      }
    },
    computed: {
    },
    created() {
    },
    methods: {
      handleCloseDialog() {
        this.$emit('closeDialog', 'pwdModifyDialogVisible')
      },
      formSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const data = {}
            data.old_password = md5(this.ruleForm.old_password)
            data.new_password = md5(this.ruleForm.new_password)
            data.new_confirmpwd = md5(this.ruleForm.new_confirmpwd)
            editPassword(data).then(res => {
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }
              this.$message.success(this.$t('user.modifwd.tips.modifySuccess'))
              setTimeout(() => {
                this.$router.push('/login')
              }, 1000)
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
  
    }
  }
</script>

<style lang="less" scoped>
  .pwd-dialog{
    .payway{
      margin-bottom: 15px;
      label{
        // width: 86px;
        display: inline-block;
        text-align: right;
        padding-right: 10px;
      }
    }
    .code-tips{
      margin-top: 89px;
      color: #b4b6be;
      font-size: 12px;
    }
    .google-key .pull-left img {
      height: 80px;
      width: 80px;
      margin-left: 15px;
      margin-right: 6px;
    }
    .google-key .pull-left .key {
      width: 210px;
      height: 40px;
      line-height: 40px;
      border-radius: 5px;
      text-align: center;
      border: 1px solid #e5e2e9;
      background: #f1eff4;
      margin: 10px  0 14px;
  }
  }
</style>
<style lang="less">
  .pwd-dialog{
    .avatar-uploader{
      width: 109px;
      display: inline-block;
    }
    .avatar-uploader .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
      border-color: #409EFF;
    }
    .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 109px;
      height: 109px;
      line-height: 109px;
      text-align: center;
    }
    .avatar {
      width: 109px;
      height: 109px;
      display: block;
    }
  }
</style>


